body {
  background: #f9fafb;
  font-family: Arial, sans-serif;
}

.container {
  text-align: center;
  margin-top: 5rem;
}

input {
  width: 60%;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
}

button {
  padding: 10px 20px;
  margin-left: 10px;
  border: none;
  background-color: #007bff;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.result {
  margin-top: 20px;
  font-size: 1.1rem;
}

a {
  color: #007bff;
  text-decoration: none;
}